"use Client";
import { useState } from "react";
const Location = () => {
  const [city, setCity] = useState<string>("");
  console.log(city);
  return (
    <div>
      <input
        type="text"
        value={city}
        onChange={(e) => setCity(e.target.value)}
      />
    </div>
  );
};

export default Location;
